import gql from 'graphql-tag'


export const Reports_Services = gql`
  query reportsServices($start: Int, $end: Int, $startDate: Date, $endDate: Date, $status: String, $type: Int) {
    reportsServices(start: $start, end: $end, startDate: $startDate, endDate: $endDate, status: $status, type: $type) {
        services {
            id
            latitude
            longitude
            date
            typeService
            packs_service {
                quantity
                pack {
                price
                }
            }
            customer {
                names
            }
            technical {
                names
            }
            engineer {
                names
            }
        }
        totalFields
      }
}`
