import React, { Fragment, useState, useEffect } from 'react'
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import { Reports_Services } from '../../queries/reports.query'
import { useLazyQuery } from '@apollo/react-hooks';
import { Button, Typography } from '@material-ui/core';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableFooter from '@material-ui/core/TableFooter';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import moment from 'moment';
import Moment from 'react-moment';
import NumberFormat from 'react-number-format';
import config from '../../config'
import axios from 'axios'
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import TextField from '@material-ui/core/TextField';
import clsx from 'clsx';
import MenuItem from '@material-ui/core/MenuItem';
import { getAttentionsTypes, listCustomers, listCenterOfAttention } from '../../api/api';

import 'date-fns';
import DateFnsUtils from '@date-io/date-fns';
import {
    MuiPickersUtilsProvider,
    KeyboardDatePicker,
} from '@material-ui/pickers';
import { FaPrint } from "react-icons/fa";


const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1,
        height: 250,
        minWidth: 290
    },
    input: {
        display: "flex",
        padding: 0,
        height: "auto"
    },
    paper: {
        padding: theme.spacing(2),
        color: theme.palette.text.secondary,
        marginTop: 10,
        margin: 5
    },
    button:
    {
        margin: theme.spacing(1),
    },
    formControl: {
        margin: theme.spacing(1),
    },
    icon: {
        width: 24,
        height: 24
    }
}))

const ReportServices = props => {

    moment().locale('es')

    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(10);
    const [totalFields, setTotalFields] = useState(0)

    const classes = useStyles()
    const [startDate, setStartDate] = useState(new Date())
    const [endDate, setEndDate] = useState(new Date())
    const [pdf, setPdf] = useState(false)
    const [dataPdf, setDataPdf] = useState('')

    const [informations, setInformations] = useState([])
    const [total, setTotal] = useState(0)
    const [serviceType, setServiceType] = useState(1)
    const [serviceStatus, setServiceStatus] = useState('')
    const [serviceTypes, setServiceTypes] = useState([])
    const [customers, setCustomers] = useState([])
    const [customer, setCustomer] = useState("")
    const [centerOfAttentions, setCenterOfAttentions] = useState([])
    const [centerOfAttention, setCenterOfAttention] = useState("")

    const serviceStatuses = [{
        id: "all",
        text: "Todas"
    }, {
        id: "accept",
        text: "Aceptada"
    }, {
        id: "reject",
        text: "Rechazada"
    }, {
        id: "resend",
        text: "Reenviada"
    }]

    useEffect(() => {
        getAttentionsTypes().then(response => {
            console.log("response", response);
            setServiceTypes(response.attentionsTypes);
        })
        listCustomers().then(response => {
            console.log("response", response);
            setCustomers(response.customers);
        })

    }, []);

    const [reportsServices, { data }] = useLazyQuery(Reports_Services, {
        fetchPolicy: 'no-cache',
        onCompleted: () => {
            let sumTotal = 0
            setTotalFields(data.reportsServices.totalFields)
            const newServices = data.reportsServices.services.map((service, _) => {
                let totalServices = 0
                // console.log('service', service)
                service.packs_service.map((pack_service, _) => {
                    totalServices += pack_service.quantity * pack_service.pack.price
                })
                sumTotal += totalServices
                service.subTotal = totalServices

                return service
            })
            setInformations(newServices)
            setTotal(sumTotal)
        }
    })

    const onChangeServiceType = event => {
        setServiceType(event.target.value)
    }

    const onChangeCenterOfAttention = event => {
        setCenterOfAttention(event.target.value)
    }
    const onChangeCustomer = event => {
        setCustomer(event.target.value)
        listCenterOfAttention({ customer: event.target.value }).then(response => {
            setCenterOfAttentions(response.centersOfAttention);
        })
    }
    const onChangeServiceStatus = event => {
        setServiceStatus(event.target.value)
    }
    const onChangeStartDate = date => {
        setStartDate(date)
    }
    const onChangeEndDate = value => {
        setEndDate(value)
    }

    const onChangePage = (_, newPage) => {
        setPage(newPage)


        reportsServices({ variables: { start: newPage, end: rowsPerPage, startDate: startDate, endDate: endDate, status: serviceStatus, type: serviceType } })
    }

    const onChangeRowsPerPage = event => {

        setRowsPerPage(+event.target.value);
        setPage(0);
    }

    const validateForm = () => {

        let errors = 0

        return errors
    }
    const onPrint = () => {

        let data = {
            data: informations,
            total: 0,
            startDate,
            endDate
        }
        // informations.forEach(element => {
        //     //   data.data.push({
        //     //     "date": moment(new Date(element.date)).format("YYYY-MM-DD") ,
        //     //     "description": element.description,
        //     //     "value": formatCurrency("es-CO", "COP", 0, element.value) 
        //     //   })
        // })
        data.total = formatCurrency("es-CO", "COP", 0, total)

        // console.log(data)  

        function formatCurrency(locales, currency, fractionDigits, number) {
            var formatted = new Intl.NumberFormat(locales, { style: 'currency', currency: currency, minimumFractionDigits: fractionDigits }).format(number);
            return formatted;
        }
        console.log('url', `${config.hostReport1}`)

        axios.post(`${config.hostReport1}`, {
            shortid: 'WaIAVAU',
            data
        }).then(function (response) {
            console.log(response);
            let pdfData = 'data:application/pdf;base64,' + response.data
            setDataPdf(pdfData)
            setPdf(true)
        }).catch(function (error) {
            console.log("error", error);
        })
    }
    const onClosePdf = () => {
        setPdf(false)
    }
    const onSubmit = e => {

        console.log('startDate', startDate)
        console.log('endDate', endDate)
        console.log('customer', customer)
        console.log('centerOfAttention', centerOfAttention)
        console.log('serviceType', serviceType)
        console.log('serviceStatus', serviceStatus)
        let errores = validateForm()
        if (errores === 0) {
            // console.log('start', startDate)
            // console.log('end', endDate)
            // reportsServices({ variables: { start: page, end: rowsPerPage, startDate: startDate, endDate: endDate, status: serviceStatus, type: serviceType } })

        }
    }

    return (

        <Fragment>
            <Container maxWidth="lg">
                <Paper className={classes.paper}>
                    <form encType={'multipart/form-data'}
                        onSubmit={e => {
                            e.preventDefault()
                            onSubmit()
                        }} >
                        <Grid container >

                            <Grid item xs={12} sm={12}>
                                <Typography>Reporte de atenciones</Typography>
                            </Grid>
                            <Grid item xs={12} sm={3}>
                                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                                    <KeyboardDatePicker
                                        disableFuture
                                        format="yyyy-MM-dd"
                                        margin="dense"
                                        id="date-picker-inline"
                                        label="Fecha inicial"
                                        value={startDate}
                                        onChange={onChangeStartDate}
                                        KeyboardButtonProps={{
                                            'aria-label': 'change date',
                                        }}
                                    />
                                </MuiPickersUtilsProvider>
                            </Grid>

                            <Grid item xs={12} sm={3}>
                                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                                    <KeyboardDatePicker
                                        format="yyyy-MM-dd"
                                        margin="dense"
                                        id="date-picker-inline"
                                        label="Fecha final"
                                        value={endDate}
                                        onChange={onChangeEndDate}
                                        KeyboardButtonProps={{
                                            'aria-label': 'change date',
                                        }}
                                    />
                                </MuiPickersUtilsProvider>
                            </Grid>

                            <Grid item xs={12} sm={3}>
                                <TextField
                                    select
                                    style={{ width: 200 }}
                                    variant="outlined"
                                    margin="dense"
                                    label="Cliente"
                                    value={customer}
                                    onChange={onChangeCustomer}>
                                    {
                                        customers.map(option => (
                                            <MenuItem key={option._id} value={option._id} >
                                                {option.name}
                                            </MenuItem>))
                                    }
                                </TextField>
                            </Grid>

                            <Grid item xs={12} sm={3}>
                                <TextField
                                    select
                                    style={{ width: 200 }}
                                    variant="outlined"
                                    margin="dense"
                                    label="Centro de atención"
                                    value={centerOfAttention}
                                    onChange={onChangeCenterOfAttention}>
                                    {
                                        centerOfAttentions.map(option => (
                                            <MenuItem key={option._id} value={option._id} >
                                                {option.title}
                                            </MenuItem>))
                                    }
                                </TextField>
                            </Grid>

                            <Grid item xs={12} sm={3}>
                                <TextField
                                    select
                                    style={{ width: 200 }}
                                    variant="outlined"
                                    margin="dense"
                                    label="Tipo de atención"
                                    value={serviceType}
                                    onChange={onChangeServiceType}>
                                    {
                                        serviceTypes.map(option => (
                                            <MenuItem key={option._id} value={option._id} >
                                                {option.type}
                                            </MenuItem>))
                                    }
                                </TextField>
                            </Grid>


                            <Grid item xs={2} sm={3}>
                                <TextField
                                    select
                                    style={{ width: 200 }}
                                    variant="outlined"
                                    margin="dense"
                                    label="Estado de la atención"
                                    value={serviceStatus}
                                    onChange={onChangeServiceStatus}>
                                    {
                                        serviceStatuses.map(option => (
                                            <MenuItem key={option.id} value={option.id} >
                                                {option.text}
                                            </MenuItem>))

                                    }
                                </TextField>
                            </Grid>

                            <Grid item xs={2} sm={12}>
                                <Button type="submit" variant="contained" color='primary' style={{ marginTop: 20 }}>Consultar</Button>
                            </Grid>
                        </Grid>
                    </form>
                </Paper>

            </Container>
            <Paper className={classes.paper}>
                <TableContainer className={classes.container} style={{ width: "100%" }}>
                    <Table stickyHeader aria-label="sticky table">
                        <TableHead>
                            <TableRow>
                                <TableCell>Fecha</TableCell>
                                <TableCell>Cliente</TableCell>
                                <TableCell>Tipo de servicio</TableCell>
                                <TableCell>Técnico</TableCell>
                                <TableCell>Ingeniero</TableCell>
                                <TableCell align="right">Total</TableCell>

                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {
                                informations.map(service => {
                                    return (
                                        <TableRow hover role="checkbox" tabIndex={-1} key={service.id}>
                                            <TableCell><Moment format="ll">{new Date(service.date)}</Moment></TableCell>
                                            <TableCell>{service.customer.names}</TableCell>
                                            <TableCell>{service.typeService === 1 ? "Residencial" : "Crédito"}</TableCell>
                                            <TableCell>{service.technical ? service.technical.names : ""}</TableCell>
                                            <TableCell>{service.engineer ? service.engineer.names : ""}</TableCell>
                                            <TableCell align="right"><NumberFormat value={service.subTotal} displayType={'text'} thousandSeparator={true} prefix={'$'} /></TableCell>

                                        </TableRow>
                                    );
                                })}
                        </TableBody>
                        {
                            informations.length > 0
                                ? <TableFooter>
                                    <TableRow hover>
                                        <TableCell>
                                            <FaPrint className={classes.icon} onClick={onPrint} />
                                        </TableCell>
                                        <TableCell colSpan={7} align="right">
                                            <Typography variant="h3">
                                                <NumberFormat value={total} displayType={'text'} thousandSeparator={true} prefix={'$'} />
                                            </Typography>
                                        </TableCell>
                                    </TableRow>
                                </TableFooter>
                                : null
                        }
                    </Table>
                </TableContainer>
                <TablePagination
                    rowsPerPageOptions={[10, 25, 100]}
                    component="div"
                    count={totalFields}
                    rowsPerPage={rowsPerPage}
                    page={page}
                    onChangePage={onChangePage}
                    onChangeRowsPerPage={onChangeRowsPerPage}
                />
            </Paper>


            <Dialog
                maxWidth="lg"
                open={pdf}
                onClose={onClosePdf}
                aria-labelledby="draggable-dialog-title">
                <DialogTitle id="draggable-dialog-title">Reporte de servicios</DialogTitle>
                <DialogContent>
                    <iframe src={dataPdf} title="Imprimir" height="900px" width="600px" ></iframe>
                </DialogContent>
                <DialogActions>

                    <Button onClick={onClosePdf} color="primary">
                        Cerrar
                    </Button>
                </DialogActions>
            </Dialog>
        </Fragment>
    )
}
export default ReportServices 